// 图形缺陷采集

var defectsCapturePictureCtrl = {
	page : undefined,
	init: function() {
		$(document).off("keydown");
		util.setBreadcrumb(["图形缺陷采集"]);
		this.loadPage();
		this.event();
	},
	event : function(){
		var _this = this;

		$("#open-picker-defectscapture",_this.page).on("click",function(e){
			$(".masked",_this.page).show();
			myApp.pickerModal('.picker-defectscapture')
		});

		$(".masked,.close-picker-btn",_this.page).on("click",function (e) {
			$(".masked",_this.page).hide();
			myApp.closeModal('.picker-defectscapture');

			var radioSelect = $('input:radio[name="duty-type-other"]:checked');
			if(radioSelect.length > 0){

				$('#duty-type-6').val(radioSelect.val());

				var otherText = $('input:radio[name="duty-type-other"]:checked+label').text();
				$('#duty-type-6+label').text(otherText);
			}
		});

		$(".other-input",_this.page).on("click",function(e){
			$('#defect-count-4').click();
			$(this).focus();
		})

		$(".file-choose-wrap label",_this.page).on("click",function(e){
			$('#fileInput').click();
		})

		$("#fileInput",_this.page).on("change",function(e){
			var files = e.target.files || e.dataTransfer.files;
			var fileName = files[0].name;
			$('.file-name').text(fileName);
		})
	},
	loadPage : function(){
		// 计算右侧三张表的高度
		var rightTableHeight = (window.innerHeight - 502) + 'px';
		// 右侧第 2 张表
		$.getJSON(app.xhrUrl+"defectsCaptureTable4.json",function(response){
			var rightTableApi2 = $('table#right-table2',this.page).DataTable({
				"scrollY": rightTableHeight,
				"ordering": false,
				"serverSide": false,
				"searching": true,
				"searchDelay": 400,
				"paging": false,
				"info": false,
				"columns": [
					{
						"data": "description",
						"name": "description",
					}
				],
				"data": response.list || []
			});
			$('#right-search2').on('keyup click',function () {
				var key = $(this)[0].value;
				rightTableApi2.search(key, false, true).draw();
			});
			$('table#right-table2 tbody').on( 'click', 'tr', function () {
				$('table#right-table2 tr.selected').removeClass('selected');
				$(this).toggleClass('selected');

				var chooseText = $('table#right-table2 tr.selected td:first-child').text();
				$('#choose-text-direction').text(chooseText);
			} );
		});
		// 右侧第 3 张表
		$.getJSON(app.xhrUrl+"defectsCaptureTable5.json",function(response){
			var rightTableApi3 = $('table#right-table3',this.page).DataTable({
				"scrollY": rightTableHeight,
				"ordering": false,
				"serverSide": false,
				"searching": true,
				"searchDelay": 400,
				"paging": false,
				"info": false,
				"columns": [
					{
						"data": "description",
						"name": "description",
					}
				],
				"data": response.list || []
			});
			$('#right-search3').on('keyup click',function () {
				var key = $(this)[0].value;
				rightTableApi3.search(key, false, true).draw();
			});
			$('table#right-table3 tbody').on( 'click', 'tr', function () {
				$('table#right-table3 tr.selected').removeClass('selected');
				$(this).toggleClass('selected');

				var chooseText = $('table#right-table3 tr.selected td:first-child').text();
				$('#choose-text-defect').text(chooseText);
			} );
		});
	},
}

module.exports = defectsCapturePictureCtrl




